<template>
  <div id="body">
    <!--中部内容-->
    <div id="content">
      <!--登录输入框背景框-->
      <div class="login-box" style="position: relative">
        <!--标题图片-->
        <p><img src="../../assets/login_images/title1.png" alt=""></p>
        <!--登录输入框-->
        <div class="login-box-left">
          <Form :model="loginData" :rules="loginRules"  label-position="top" ref="loginForm" @keyup.enter.native="handleSubmit" id="loginForm">
            <FormItem prop="username" class="fromItems">
              <i><img src="../../assets/login_images/user.png" alt=""></i>
              <Input size="large" type="text" v-model="loginData.username" placeholder="请输入用户名" icon=""/>
            </FormItem>
            <FormItem prop="password" class="fromItems">
              <i class="locked"><img src="../../assets/login_images/locked.png" alt=""></i>
              <Input size="large" type="password" v-model="loginData.password" placeholder="请输入密码" icon=""/>
            </FormItem>
            <FormItem  prop="codetest" class="verificationCode_box">
              <Input size="large" class="verificationCode_text" v-model="loginData.codetest" type="text" placeholder="请输入验证码"/>
              <div class="verificationCode">
                <Codetest ref="codetest" @getCodetest="getCodetest"></Codetest>
              </div>
            </FormItem>
            <!--<p>
              <Checkbox>记住密码</Checkbox>
            </p>-->
            <FormItem class="fromItems fromBtns" style="margin-top: 20px">
              <Checkbox>记住密码</Checkbox>
              <Button size="large" long type="primary" @click="handleSubmit" ref="submit" :loading="logining">登录</Button>
            </FormItem>
          </Form>
        </div>
      </div>
    </div>
    <!--底部内容：展示的是版权信息-->
    <div id="footer">
      <p>
        <span>Copyright © 安徽省生态环境厅  版权所有</span>
        <span>技术支持单位：生态环境部卫星环境应用中心</span>
      </p>
    </div>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
import { login } from '@/api/login'
import Codetest from './Codetest'

export default {
  name: 'Login',
  data () {
    const validateCode = (rule, value, callback) => {
      if (value !== this.loginData.verificationCode) {
        callback(new Error('验证码错误'))
      } else {
        callback()
      }
    }
    return {
      /*  登录框输入的数据  */
      loginData: {
        username: '',
        password: '',
        verificationCode: ''
      },
      /* 登录窗口验证的规则 */
      loginRules: {
        username: [{
          required: true, message: '请填写手机号/邮箱/用户名', trigger: 'blur'
        }],
        password: [{
          required: true, message: '请填写密码', trigger: 'blur'
        }],
        codetest: [
          { required: true, message: '请填写验证码', trigger: 'blur' },
          { validator: validateCode, trigger: 'blur' }
        ]
      },
      // 登录按钮loading
      logining: false
    }
  },
  components: {
    Codetest
  },
  methods: {
    getCodetest (identifyCode) {
      this.loginData.verificationCode = identifyCode
    },
    /* 登录请求的方法 */
    handleSubmit () {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          this.logining = true
          login({
            validate: true,
            data: {
              username: this.loginData.username,
              password: this.loginData.password,
              type: 0
            }
          }).then(res => {
            /* 修改验证码 */
            this.$refs.codetest.refreshCode()
            if (res.data.code === '0000') {
              this.logining = false
              this.$store.commit('setloginInfor', res.data.data)
              sessionStorage.removeItem('loginInfo')
              // 保存到session里面
              sessionStorage.setItem('loginInfo', JSON.stringify(res.data.data))
              Cookies.set('token', res.data.data.token)
              Cookies.set('uuid', res.data.data.uuid)
              this.$router.push({ name: 'dataCenter' })
            } else {
              this.logining = false
              this.$Notice.error({ title: res.data.msg })
            }
          }).catch(() => {
            this.logining = false
            this.$Notice.error({ title: '服务器错误' })
          })
        }
      })
    }
  },
  created () {
    this.$store.commit('setloginInfor', {})
    sessionStorage.removeItem('loginInfo')
  }
}
</script>

<style scoped  lang="scss" rel="stylesheet/scss" >
  /*撑起整个页面*/
#body{
  width:100%;
  height:100%;
  background: url("../../assets/login_images/loginBack.png") no-repeat center center;
  background-size: 100% 100%;
  overflow: hidden;
}
  /*中部内容的样式*/
#content{
  width: 60%;
  min-width: 700px;
  height: 100%;
  background:url("../../assets/login_images/fromBack.png") top right no-repeat;
  background-size: 100% 100%;
  float: right;
  img{
    width:70%;
    display: inline-block;
  }
  /* 登录输入框的样式 */
  .login-box{
    &>p {
      width: 60%;
      margin: 10% auto 10%;
      transform: translateX(15%);
      img {
        width: 100%;
      }
    }
    .login-box-left{
      width: 420px;
      padding: 30px 45px;
      text-align: center;
      background: #fff;
      border-radius: 8px;
      margin: auto;
      transform: translateX(20%);
      /*修改input验证报错高度*/
      .ivu-form-item{
        margin-bottom: 20px;
        .ivu-form-item-error-tip{
          font-size: 10px;
          padding-top: 2px;
        }
      }
      p{
        margin:0;
        text-align: left;
        line-height: 22px;
      }
      .fromItems{
        margin-bottom:20px;
        position: relative;
        i {
          width: 16px;
          height: 16px;
          display: block;
          position: absolute;
          z-index: 1000;
          right: 10px;
          top: 5px;
          img {
            width: 100%;
            height: 100%;
          }
          &.locked {
            width: 18px;
            height: 18px;
            top: 7px;
          }
        }
      }
      .fromItems >>> input {
        height: 38px !important;
      }
      .verificationCode_box{
        margin-bottom: 10px;
        position: relative;
        .verificationCode{
          position: absolute;
          width:75px;
          height:28px;
          background: #ddd;
          right: 10px;
          top: 4px;
        }
      }
      .fromBtns {
        margin-top: 30px !important;
        margin-bottom: 0;
        overflow: hidden;
        .ivu-checkbox-wrapper {
          width: 50%;
          float: left;
          text-align: left;
          margin: 0;
        }
        .ivu-btn {
          width: 50%;
          float: right;
        }
      }
    }
  }
}
/*底部内容的样式*/
#footer{
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  height: 42px;
  line-height: 18px;
  p{
    text-align: center;
    font-size: 12px;
    color: #fff;
    letter-spacing: 1px;
    span {
      display: block;
    }
  }
}
</style>
